<template>
	<div class="layout-topbar">
		<router-link to="/" class="layout-topbar-logo">
			<img alt="Logo" :src="topbarImage()" />
			<span>&nbsp;A Bot called Seraphina&nbsp;</span>
		</router-link>
		<router-link to="/" class="layout-topbar-logo2">
			<span>Ongoing&nbsp;</span>
			<span>Trade: </span>
			&nbsp;
			<span id="on_going_pos"></span>
			<span id="cur_cash_text"></span>
			&nbsp;
			<span id="cur_prof_cash"></span>
			&nbsp;
			<span id="cur_perc_text"></span>
			&nbsp;
			<span id="cur_prof_perc"></span>
		</router-link>
		<router-link to="/" class="layout-topbar-logo3">
			<img src="images/Twitterin_logo.svg"/>
			<span>@Olwar2</span>
		</router-link>
	</div>
</template>

<script>
import axios from 'axios';

export default {
    methods: {
		topbarImage() {
			return this.$appState.darkTheme ? 'images/mummo_iloinen_vektori.svg' : 'images/mummo_iloinen_vektori.svg';
		},
    },
	computed: {
		darkTheme() {
			return this.$appState.darkTheme;
		}
	},
	mounted() {
		var cur_perc = document.getElementById("cur_prof_perc");
		var cur_cash = document.getElementById("cur_prof_cash");
		var on_going_pos = document.getElementById("on_going_pos")
		axios
			.get('https://seraphinabot.com/')
			.then(response => {
			if (parseInt(response.data[3].cur_cash) != 0) {
				cur_perc.textContent = response.data[3].cur_perc + "%"; cur_cash.textContent = response.data[3].cur_cash + "";
				if (parseFloat(cur_perc.textContent) >= 0) {
					cur_perc.setAttribute("style", "color:#00bb7e")
					cur_cash.setAttribute("style", "color:#00bb7e")
				} else {
					cur_perc.setAttribute("style", "color:red")
					cur_cash.setAttribute("style", "color:red")
				}
			}
			else {
				on_going_pos.textContent = "No"; on_going_pos.setAttribute("style", "color:#ff4f28");
			}
			});
	}
}
</script>