<template>
	<div class="grid">
		<div class="col-12 xl:col-6">
			<div class="card">
				<h5>Recent Trades</h5>
				<DataTable :value="products" :rows="5" :paginator="true" responsiveLayout="scroll">
					<Column style="width:40%">
						<template #header>
							Cryptocurrency
						</template>
						<template #body>
							<img :src="'images/product/eth.jpg'" width="100" />
						</template>
					</Column>
					<Column field="month" header="Date" :sortable="true" style="width:40%"></Column>
					<Column id="outcome" field="money" header="Outcome" :sortable="true" style="width:40%">
						<template>
						</template>
					</Column>

				</DataTable>
			</div>
		</div>
		<div class="col-12 xl:col-6">
			<div class="card">
				<h5>Total Profits</h5>
				<span id="profits_perc"></span>
				&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
				<span id="profits_money"></span>
				<Chart type="line" :data="lineData" />
			</div>
		</div>
	</div>
</template>

<script>
import ProductService from '../service/ProductService';
import axios from 'axios';

window.onload = dostuff;

function dostuff() {
	var span_perc = document.getElementById("profits_perc");
	var span_money = document.getElementById("profits_money");
	axios.get('https://seraphinabot.com').then(response => {
		span_perc.textContent = response.data[2].profits_perc;
		if (parseInt(span_perc.textContent) < 0) {
			span_perc.setAttribute("style", "color:red")
		} else {
			span_perc.setAttribute("style", "color:#00bb7e")
		}
	});
	axios.get('https://seraphinabot.com').then(response => 
	{span_money.textContent = response.data[2].profits_cash;
	if (parseInt(span_money.textContent) < 0) {
		span_money.setAttribute("style", "color:red")
	} else {
		span_money.setAttribute("style", "color:#00bb7e")
	}
	});
}

export default {
	data() {
		return {
			products: null,
			items: [
                {label: 'Add New', icon: 'pi pi-fw pi-plus'},
                {label: 'Remove', icon: 'pi pi-fw pi-minus'}
            ],
			profitData: [],
			plotLabels: []
		}
	},
	productService: null,
	created() {
		this.productService = new ProductService();
	},
	computed: {
		lineData() {
			return {
				labels: this.plotLabels,
				datasets: [
					{
						label: 'Money',
						data: this.profitData,
						fill: false,
						backgroundColor: this.color,
						borderColor: this.color2, //'#00bb7e',
						tension: 0.0
					},
				]
			}
		}
	},
	mounted() {
		this.productService.getProductsSmall()
		.then(data => {
			this.products = data[1];
			});
		axios
			.get('https://seraphinabot.com')
			.then(response => {
				this.profitData = response.data[0].data[0].data;
				this.plotLabels = response.data[0].data[0].labels;
				if (parseInt(response.data[2].profits_perc) < 0) {
					this.color = '#f84f2a';
					this.color2 = '#f84f2a';
				}
				else {
					this.color = '#00bb7e';
					this.color2 = '#00bb7e';
				}
			});
	},
	methods: {
		formatCurrency(value) {
			return value.toLocaleString('en-US', {style: 'currency', currency: 'USD'});
		}
	}
}
</script>